@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
html,body{margin:0;padding:0;background-color:#007a47;font-family:"Raleway",sans-serif;font-weight:400}@media screen and (max-width: 425px){.todo{display:flex;position:relative;background-color:#33956c;width:80%;margin:1em auto;border-radius:20px 0px 20px 20px}.todo__title{margin-top:40%;font-weight:400;font-size:3em;color:white;text-align:center}.todo__checkbox{-webkit-appearance:none;-moz-appearance:none;appearance:none;background-color:#33956c;margin:10px;height:40px;width:40px;border:2px solid white;background:none;color:white;border-radius:10px;display:flex}.todo__checkbox:checked{opacity:0.7}.todo__checkbox:checked::before{content:"\2716";margin:0 auto;background:none;border:none;font-size:2em;height:40px}.todo__text{margin:10px;height:40px;font-size:2em;display:inline-block;color:white}.todo__btn{position:absolute;float:right;top:-10px;right:-15px;color:#e65137;background:none;border:none}.todo__btn__icon{height:25px;width:25px;background-color:white;border-radius:15px}.todo__form{border-radius:20px 0px 20px 20px;border:3px solid #33956c;width:80%;margin:0 auto;display:flex}.todo__form__input{margin:10px;background:none;border:none;width:80%;font-size:1.5em;height:40px}.todo__form__input::-webkit-input-placeholder{font-family:"Raleway",sans-serif;font-weight:400;color:white;opacity:0.6}.todo__form__input:-ms-input-placeholder{font-family:"Raleway",sans-serif;font-weight:400;color:white;opacity:0.6}.todo__form__input::placeholder{font-family:"Raleway",sans-serif;font-weight:400;color:white;opacity:0.6}.todo__form__input:focus{color:white;font-family:"Raleway",sans-serif}.todo__form__btn{margin:10px;height:40px;width:40px;border:2px solid white;background:none;color:white;border-radius:10px}.todo__form__btn::before{content:"\271A";font-weight:100;font-size:2em}.completed{text-decoration:line-through;opacity:0.4}}@media screen and (min-width: 426px){.todo{display:flex;position:relative;margin:2em auto;width:80%;background-color:#33956c}.todo__title{font-size:5em;text-align:center;color:white;margin-top:10%}.todo__checkbox{-webkit-appearance:none;-moz-appearance:none;appearance:none;margin:20px;height:60px;width:60px;border:2px solid white;background:none;color:white;border-radius:10px;display:flex}.todo__checkbox:checked{opacity:0.7}.todo__checkbox:checked::before{content:"\2716";margin:0 auto;background:none;border:none;font-size:3em;height:40px}.todo__text{margin:20px;height:60px;display:inline-block;font-size:3em;color:white}.todo__btn{position:absolute;top:20px;right:25px;float:right;width:50px;height:50px;background:none;border:none}.todo__btn__icon{font-size:3.5em;color:white;background:none;border:none}.todo__form{width:80%;margin:0 auto;display:flex}.todo__form__input{margin:20px 5px;background:none;width:80%;font-size:2em;height:66px;border:2px solid #33956c;border-radius:10px;padding-left:1em;color:white}.todo__form__input::-webkit-input-placeholder{font-family:"Raleway",sans-serif;font-weight:400;color:white;opacity:0.7}.todo__form__input:-ms-input-placeholder{font-family:"Raleway",sans-serif;font-weight:400;color:white;opacity:0.7}.todo__form__input::placeholder{font-family:"Raleway",sans-serif;font-weight:400;color:white;opacity:0.7}.todo__form__btn{margin:20px;border:none;height:70px;padding:10px 0;width:40%;background:#33956c;color:white;border-radius:10px}.todo__form__btn::before{content:"Add task";font-weight:100;font-size:2.5em}.completed{text-decoration:line-through;opacity:0.4}}@media screen and (min-width: 1023px){.todo{width:60%}.todo__form{width:60%}}@media screen and (min-width: 1339px){.todo{width:50%}.todo__form{width:50%}}

